/* montserrat-regular - cyrillic_latin */
@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/montserrat-v14-cyrillic_latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
  url('../fonts/montserrat-v14-cyrillic_latin-regular.woff') format('woff') /* Modern Browsers */
}
/* montserrat-700 - cyrillic_latin */
@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 700;
  src: url('../fonts/montserrat-v14-cyrillic_latin-700.woff2') format('woff2'), /* Super Modern Browsers */
  url('../fonts/montserrat-v14-cyrillic_latin-700.woff') format('woff') /* Modern Browsers */
}
/* montserrat-900 - cyrillic_latin */
@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 900;
  src: url('../fonts/montserrat-v14-cyrillic_latin-900.woff2') format('woff2'), /* Super Modern Browsers */
  url('../fonts/montserrat-v14-cyrillic_latin-900.woff') format('woff') /* Modern Browsers */
}

* {
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", "Arial", sans-serif;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

h1 {
  color: red;
}

.main {
  &__section {
    color: green;
    font-weight: 900;
    font-size: 30px;
  }
}
